 National Sales Manager."),_c('br'),_c('br'),_vm._v("Both users manage their own teams in the workplace and strive to be a step ahead of the rest by developing unprecedented standards for their team which will eventually set entirely new standards for the business. Both users are valued for their ingenuity and resourcefulness, making them ideal candidates to launch a new cloud platform.")]),_c('img',{attrs:{"src":require("@/assets/flytpersona1.jpg")}}),_c('img',{attrs:{"src":require("@/assets/flytpersona2.jpg")}})]),_c('ContentBlock',{staticClass:"project-text",attrs:{"bgColor":"#FFFFFF"}},[_c('h4',[_vm._v("User Stories")]),_c('p',[_vm._v("I realized not all users would need features like sharing items and real time collaboration,many just want the basics of creating documents, spreadsheets, and presentations. I researched what new or returning users would actually want and divided these tasks into low, medium, and high priorities to match the needs of each user.")]),_c('img',{attrs:{"src":require("@/assets/flytuserstories.jpg")}}),_c('h4',[_vm._v("User Flows")]),_c('p',[_vm._v("The Flyt application has many easy to use features directly from the dashboard. Users can upgrade FLYT storage to Violet or Ultra Violet packages and upload files such as documents, photos, and videos. For school and office work, users can create and save useful documents. FLYt Ink for writing documents, FLYT Grid for spreadsheets, FLYt Demo for presentations, and FLYt Capture for photos and videos. Users can then share these documents with other users to engage in real time collaboration.")]),_c('img',{attrs:{"src":require("@/assets/flytuserflow.jpg")}})]),_c('ContentBlock',{staticClass:"project-text",attrs:{"bgColor":"#EFEFEF"}},[_c('h3',[_vm._v("The Framework")]),_c('h4',[_vm._v("Low Fi Mockups")]),_c('p',[_vm._v("Once creating the wireframes and doing user tests, I observed where users were getting stuck and what enhancements were needed for the real prototype."),_c('br'),_c('br'),_vm._v