        methods:{
            carousel(){
                var i;
                var x = document.getElementsByClassName("slide-text");
                for (i = 0; i < x.length; i++) {
                    x[i].style.display = "none";  
                }
                this.myIndex++;
                if (this.myIndex > x.length) {this.myIndex = 1}    
                x[this.myIndex-1].style.display = "block";  
                setTimeout(function(){
                    this.carousel();
                }.bind(this), 10000);
            }
        },
        mounted(){
            this.carousel()
        },
    }
