<template>
    <div class="card">
        <img :src="require(`@/assets/${image}`)" />
        <div class="text">
            <h2>{{ title }}</h2>
            <p>
                <slot></slot>
            </p>
            <router-link id="next" :to="link" :style="{'color':color }" @click.native="scrollToTop">Read the Case Study</router-link>
        </div>
    </div>
</template>

<script>
    export default {
        name: 'Card',
        props:{
            image: String,
            title: String,
            link: String,
            color: String          
        },
        methods: { 
           scrollToTop() {
               setTimeout(function(){
                   window.scrollTo({
                    top: 0,
                    behavior: 'smooth'
                });
               }, 1000);
           }
        }
    }
</script>

